import React, { useState } from "react";
import Video from "../../videos/video.mp4";
import { Button } from "../ButtonElements";
import {
  FirstContainer,
  FirstBg,
  VideoBg,
  FirstContent,
  FirstH1,
  FirstP1,
  FirstBtnWrapper,
  ArrowForward,
  ArrowRight,
  FirstAvatar,
} from "./FirstElements";

const FirstSection = () => {
  const [hover, setHover] = useState(false);

  const onHover = () => {
    setHover(!hover);
  };
  return (
    <FirstContainer id="home">
      <FirstBg>
        <VideoBg autoPlay loop muted src={Video} type="video/mp4" />
      </FirstBg>
      <FirstContent>
        <FirstAvatar src="data:image/jpeg;base64," />
        <FirstH1>Anish Gyawali</FirstH1>
        <FirstP1>
          Hi, I am Anish. I am looking for internship opportunities.
        </FirstP1>
        <FirstP1>I am a full stack web developer</FirstP1>
        <FirstBtnWrapper>
          <Button
            to="resume"
            onMouseEnter={onHover}
            onMouseLeave={onHover}
            primary="true"
            dark="true"
          >
            Check Resume {hover ? <ArrowForward /> : <ArrowRight />}
          </Button>
        </FirstBtnWrapper>
      </FirstContent>
    </FirstContainer>
  );
};

export default FirstSection;
